import { Outlet } from "react-router-dom";

import { Box, Divider, Paper, Grid } from "components";
import { Logo } from "icons";
import { LanguageSwitcher } from "widget";

import styles from "./authorization.module.css";

export const Authorization = () => {
  return (
    <>
      <Box
        sx={{
          position: "absolute",
          width: "100%",
        }}
      >
        <Grid
          container
          xsOffset={1.76}
          xs={8.69}
          justifyContent="space-between"
          alignItems="center"
          height="72px"
        >
          <Logo />
          <LanguageSwitcher />
        </Grid>
        <Divider />
      </Box>
      <Box className={styles.background} sx={{}} />
      <Box className={styles.container}>
        <Box sx={{ minHeight: 536 }}>
          <Paper
            sx={{
              padding: 4,
              boxShadow: "0px 4px 30px rgba(24, 25, 28, 0.08)",
              borderRadius: 3,
              minWidth: 414,
            }}
          >
            <Grid container flexDirection="column">
              <Outlet />
            </Grid>
          </Paper>
        </Box>
      </Box>
    </>
  );
};
