import { useState, useCallback, memo } from "react";
import { Outlet } from "react-router-dom";

import { DictionaryLanguage } from "dictionary";
import { setLanguage, getLanguage } from "utils";

import { LanguageContext } from "../";

export const LanguageProvider = memo(() => {
  const [activeLanguage, setActiveLanguage] = useState(
    () => getLanguage() || DictionaryLanguage.ru
  );

  const handleSetActiveLanguage = useCallback(
    (language: DictionaryLanguage) => {
      setLanguage(language);
      setActiveLanguage(language);
    },
    []
  );

  return (
    <LanguageContext.Provider
      value={{ activeLanguage, setActiveLanguage: handleSetActiveLanguage }}
    >
      <Outlet />
    </LanguageContext.Provider>
  );
});
