import { useCallback } from "react";
import { useFormState } from "react-hook-form";

import { Button } from "components";
import { INPUT_MASK_PHONE } from "constant";
import {
  TypographyDictionary,
  Translates,
  FormInputMask,
  FormTextField,
} from "widget";

import { Steps } from "../../register.constants";

import { SendSms } from "./children";
import type { StepThreeProps } from "./stepThree.interface";

export const StepThree = (props: StepThreeProps) => {
  const { onChangeStep, control } = props;

  const { errors } = useFormState({ control });

  const handleOnChangeStep = useCallback(
    (step: Steps) => () => {
      onChangeStep(step);
    },
    [onChangeStep]
  );

  return (
    <>
      <TypographyDictionary
        variant="body1"
        sx={{
          lineHeight: 1.8,
          letterSpacing: "0.085em",
          marginBottom: 2,
        }}
      >
          
      </TypographyDictionary>
      <FormInputMask
        name="phone"
        control={control}
        fullWidth
        label={<Translates></Translates>}
        maskOptions={{ mask: INPUT_MASK_PHONE }}
        sx={{ marginBottom: 2.5 }}
      />
      <FormTextField
        autoFocus
        name="smsCode"
        control={control}
        label={<Translates>-</Translates>}
        type="one-time-code"
        sx={{ marginBottom: 2.5 }}
      />
      <Button
        onClick={handleOnChangeStep(Steps.four)}
        disabled={Boolean(errors?.phone) || Boolean(errors?.smsCode)}
        fullWidth
        variant="contained"
        size="large"
        color="black"
        sx={{ marginBottom: 2.5, textTransform: "none" }}
      >
        <TypographyDictionary color="white" sx={{ letterSpacing: "0.1em" }}>
          
        </TypographyDictionary>
      </Button>
      <SendSms />
    </>
  );
};
