import type { Components, Theme } from "@mui/material";

export const MuiButton: Components<Theme>["MuiButton"] = {
  styleOverrides: {
    root: () => ({
      borderRadius: 8,
    }),
  },
  variants: [
    {
      props: { size: "large" },
      style: {
        minHeight: 58,
      },
    },
    {
      props: { variant: "outlined" },
      style: {
        border: "1.5px solid",
      },
    },
  ],
};
