import type { Theme } from "@mui/material";

export const sxToggleButton = ({ spacing, palette }: Theme) => {
  const {
    outlinedInput: { background, border },
  } = palette;

  const lineGradientColor = `92.09deg, #383DD9 0%, #696EFF 74.14%, #F7ABFF 122.08%`;
  const lineGradientBackground = `linear-gradient(${background}, ${background}) padding-box, linear-gradient(${lineGradientColor}) border-box`;

  return {
    background: background,
    borderRadius: 2,
    border: `1.5px solid ${border}`,
    textTransform: "none",
    columnGap: 1,

    "> span": {
      color: border,
    },

    ":not(.Mui-selected) > *": {
      opacity: 0.6,
    },

    ":hover": {
      background: border,
      border: `1.5px solid ${background}`,
    },

    "&.Mui-selected": {
      background: lineGradientBackground,
      border: "1.5px solid transparent",

      "&.MuiToggleButtonGroup-grouped": {
        ":not(:first-of-type)": {
          borderLeft: `1.5px solid transparent`,
        },
      },

      ":hover": {
        background: lineGradientBackground,
      },
    },

    "&.MuiToggleButtonGroup-grouped": {
      ":not(:first-of-type)": {
        marginLeft: "auto",
        borderLeft: `1.5px solid ${border}`,
        borderTopLeftRadius: spacing(1),
        borderBottomLeftRadius: spacing(1),
      },

      ":not(:last-of-type)": {
        borderTopRightRadius: spacing(1),
        borderBottomRightRadius: spacing(1),
      },
    },
  };
};
