import { useMemo, useCallback } from "react";

import { FileLock } from "icons";
import { IconButton, InputAdornment, Tooltip } from "components";
import { Translates } from "widget";
import { generatePassword } from "utils";

import type {
  HandleMouseDown,
  UseGeneratePasswordProps,
} from "./useGeneratePassword.interface";

export const useGeneratePassword = (props: UseGeneratePasswordProps) => {
  const { onClickGeneratePassword } = props;

  const handleOnClickGeneratePassword = useCallback(() => {
    if (onClickGeneratePassword) {
      onClickGeneratePassword(generatePassword());
    }
  }, [onClickGeneratePassword]);

  const handleMouseDown = useCallback((event: HandleMouseDown) => {
    event.preventDefault();
  }, []);

  const endAdornment = useMemo(() => {
    return (
      <Tooltip
        title={<Translates>  </Translates>}
        placement="top"
        disableInteractive
      >
        <InputAdornment position="end">
          <IconButton
            aria-label="generate password"
            onClick={handleOnClickGeneratePassword}
            onMouseDown={handleMouseDown}
            edge="end"
          >
            <FileLock />
          </IconButton>
        </InputAdornment>
      </Tooltip>
    );
  }, [handleOnClickGeneratePassword, handleMouseDown]);

  return { endAdornment };
};
