import { useCallback, useState } from "react";

import { Button, CountdownTimer } from "components";
import { TypographyDictionary } from "widget";

export const SendSms = () => {
  const [isActiveResendSms, setIsActiveResendSms] = useState(true);

  const handleOnTimeEnd = useCallback(() => {
    setIsActiveResendSms(true);
  }, [setIsActiveResendSms]);

  const handleResendSms = useCallback(() => {
    setIsActiveResendSms(false);
  }, [setIsActiveResendSms]);

  return (
    <Button
      onClick={handleResendSms}
      disabled={!isActiveResendSms}
      fullWidth
      variant="outlined"
      size="large"
      sx={{ textTransform: "none" }}
      color="black"
    >
      {isActiveResendSms ? (
        <TypographyDictionary>   </TypographyDictionary>
      ) : (
        <>
          <TypographyDictionary variant="subtitle2">
               
          </TypographyDictionary>
          &nbsp;
          <CountdownTimer onTimerEnd={handleOnTimeEnd} initialTime={500} />
        </>
      )}
    </Button>
  );
};
