import type { GenerateRandomNumber } from "./generateRandomNumber.interface";

export const generateRandomNumber = (arg: GenerateRandomNumber) => {
  let num = 0;

  if (typeof arg === "string") {
    num = arg.length;
  }

  if (typeof arg === "number") {
    num = arg;
  }

  return Math.floor(Math.random() * num);
};
