import { Typography } from "components";
import { Translates } from "widget";

import type { TypographyDictionaryProps } from "./TypographyDictionary.interface";

export const TypographyDictionary = (props: TypographyDictionaryProps) => {
  const { children, ...rest } = props;

  return (
    <Typography {...rest}>
      <Translates>{children}</Translates>
    </Typography>
  );
};
