import { FORM_MIN_LENGTH_PASSWORD } from "constant";

import type {
  GetLineProgress,
  GetLineProgressReturn,
} from "./PasswordProgressInput.interface";

export const getLineProgress = (
  props: GetLineProgress
): GetLineProgressReturn => {
  const { invalid, value } = props;
  if (invalid) {
    return {
      progress: 33,
      color: "error",
    };
  }

  if (value) {
    const lenValue = value.length;

    if (lenValue < FORM_MIN_LENGTH_PASSWORD) {
      return {
        progress: 33,
        color: "error",
      };
    }

    if (lenValue <= 8) {
      return {
        progress: 66,
        color: "warning",
      };
    }

    return {
      progress: 100,
      color: "success",
    };
  }

  return {
    progress: 0,
    color: "info",
  };
};
