import { useController } from "react-hook-form";

import { Translates, InputMask } from "widget";
import type { InputMaskValueType } from "widget";

import type { FieldValues } from "../form.interface";
import type { FormInputMaskProps } from "./formInputMask.interface";
import { useCallback } from "react";

export const FormInputMask = <TFieldValues extends FieldValues = FieldValues>(
  props: FormInputMaskProps<TFieldValues>
) => {
  const { name, rules, shouldUnregister, control, defaultValue, ...rest } =
    props;

  const {
    field,
    fieldState: { error, invalid },
  } = useController({
    name,
    rules,
    shouldUnregister,
    defaultValue,
    control,
  });

  const handleOnAccept = useCallback(
    (val: InputMaskValueType) => {
      field.onChange(val);
    },
    [field]
  );

  const errorMessage = error?.message || false;

  return (
    <InputMask
      {...rest}
      helperText={errorMessage && <Translates>{errorMessage}</Translates>}
      FormHelperTextProps={{
        sx: {
          whiteSpace: "pre",
        },
      }}
      error={invalid}
      onAccept={handleOnAccept}
      onBlur={field.onBlur}
      value={field.value}
      name={field.name}
      inputRef={field.ref}
    />
  );
};
