import type { AuthorizationHeaderProps } from "./authorizationHeader.interface";

import { Divider, Grid } from "components";
import { Logo } from "icons";
import { TypographyDictionary } from "widget";

export const AuthorizationHeader = (props: AuthorizationHeaderProps) => {
  const { title } = props;

  return (
    <>
      <Grid
        container
        justifyContent="space-between"
        alignItems="center"
        marginBottom={4}
      >
        <Logo />
        <TypographyDictionary
          sx={{
            opacity: 0.6,
            fontSize: 18,
            fontWeight: 500,
            lineHeight: 1.6,
            letterSpacing: "0.095em",
          }}
          color={({ palette }) => palette.black.main}
        >
          {title}
        </TypographyDictionary>
      </Grid>
      <Divider sx={{ marginBottom: 4 }} />
    </>
  );
};
