import { useCallback } from "react";

import { Button, Grid, Typography } from "components";
import { DictionaryWords } from "dictionary";
import { TypographyDictionary, Translates, FormTextField } from "widget";
import { InfoOutlined } from "icons";

import { Steps } from "../../register.constants";

import { PasswordProgressInput } from "./children";
import type { StepFourProps } from "./stepFour.interface";

export const StepFour = (props: StepFourProps) => {
  const { onChangeStep, control } = props;

  const handleOnChangeStep = useCallback(
    (step: Steps) => () => {
      onChangeStep(step);
    },
    [onChangeStep]
  );

  return (
    <>
      <FormTextField
        autoFocus
        name="name"
        control={control}
        label={<Translates></Translates>}
        type="username"
        sx={{ marginBottom: 2.5 }}
      />
      <FormTextField
        name="email"
        control={control}
        label={<Translates>{DictionaryWords.email}</Translates>}
        type="email"
        sx={{ marginBottom: 2.5 }}
      />
      <PasswordProgressInput control={control} />
      <Button
        onClick={handleOnChangeStep(Steps.three)}
        fullWidth
        variant="contained"
        size="large"
        color="black"
        sx={{ marginBottom: 2, textTransform: "none" }}
      >
        <TypographyDictionary color="white"></TypographyDictionary>
      </Button>
      <Grid container gap={1}>
        <InfoOutlined
          sx={({ typography, palette }) => ({
            fill: palette.text.secondary,
            width: typography.pxToRem(18),
            height: typography.pxToRem(18),
          })}
        />
        <Typography
          whiteSpace="pre-line"
          sx={({ typography, palette }) => ({
            fontSize: typography.pxToRem(13),
            color: palette.text.secondary,
            letterSpacing: "0.08em",
          })}
        >
          <Translates> ,  </Translates>
          &nbsp;
          <Typography
            component="span"
            sx={{
              textDecoration: "underline",
              fontSize: "inherit",
              letterSpacing: "inherit",
            }}
          >
            <Translates>{`\n `}</Translates>
          </Typography>
        </Typography>
      </Grid>
    </>
  );
};
