import { useIMask } from "react-imask";

import { TextField } from "components";

import type { InputMaskProps } from "./InputMask.inteface";

export const InputMask = (props: InputMaskProps) => {
  const { InputProps, maskOptions, value, onAccept, ...rest } = props;

  const { value: imaskValue, ref } = useIMask(
    {
      ...maskOptions,
    },
    {
      onAccept,
    }
  );

  return (
    <TextField
      {...rest}
      value={value || imaskValue}
      InputProps={{
        ...InputProps,
        inputRef: ref,
      }}
    />
  );
};
