import { yupResolver } from "@hookform/resolvers/yup";
import { useForm } from "react-hook-form";

import { Checkbox, Button, Grid, Divider } from "components";
import { DictionaryWords } from "dictionary";
import { RoutesMap } from "routes";
import {
  TypographyDictionary,
  NavLink,
  Translates,
  FormTextField,
} from "widget";
import { useVisiblePassword } from "hooks";
import { AuthorizationHeaderLayout } from "layout";

import { schema } from "./login.schema";
import type { OnSubmitLogin, FormLogin } from "./login.interface";

export const Login = () => {
  const { control, handleSubmit } = useForm<FormLogin>({
    resolver: yupResolver(schema),
    defaultValues: {
      email: "",
      password: "",
    },
  });

  const visiblePassword = useVisiblePassword();

  const onSubmit: OnSubmitLogin = (data) => {
    console.log(data);
  };

  return (
    <>
      <AuthorizationHeaderLayout title={DictionaryWords.entrance} />
      <form onSubmit={handleSubmit(onSubmit)}>
        <Grid container flexDirection="column">
          <FormTextField
            id="login-email-input"
            name="email"
            control={control}
            label={<Translates>{DictionaryWords.email}</Translates>}
            InputProps={{
              autoComplete: "email",
            }}
            sx={{ marginBottom: 2.5 }}
          />
          <FormTextField
            id="login-password-input"
            name="password"
            control={control}
            label={<Translates>{DictionaryWords.password}</Translates>}
            InputProps={{
              autoComplete: "current-password",
              ...visiblePassword,
            }}
            sx={{ marginBottom: 2.5 }}
          />
        </Grid>
        <Grid
          container
          justifyContent="space-between"
          alignItems="center"
          marginBottom={3.5}
        >
          <Grid container alignItems="center" spacing={1}>
            <Checkbox size="small" />
            <TypographyDictionary
              variant="body2"
              color={({ palette }) => palette.black.main}
            >
              {DictionaryWords.rememberMe}
            </TypographyDictionary>
          </Grid>
          <NavLink to={RoutesMap.home} sx={{ position: "relative" }}>
            <TypographyDictionary
              variant="body2"
              color={({ palette }) => palette.black.main}
            >
              {DictionaryWords.forgotPassword}
            </TypographyDictionary>
            <Divider sx={{ position: "absolute", bottom: 0, width: "100%" }} />
          </NavLink>
        </Grid>
        <Button
          fullWidth
          variant="contained"
          size="large"
          color="black"
          sx={{ marginBottom: 1.5, textTransform: "none" }}
          type="submit"
        >
          <TypographyDictionary color="white">
            {DictionaryWords.signIn}
          </TypographyDictionary>
        </Button>
      </form>
      <NavLink
        to={RoutesMap.register}
        variant="outlined"
        color={({ palette }) => palette.black.main}
      >
        <Translates>{DictionaryWords.signUp}</Translates>
      </NavLink>
    </>
  );
};
