import { useController } from "react-hook-form";

import { TextField } from "components";
import { Translates } from "widget";

import type { FieldValues } from "../form.interface";
import type { FormTextFieldProps } from "./formTextField.interface";

export const FormTextField = <TFieldValues extends FieldValues = FieldValues>(
  props: FormTextFieldProps<TFieldValues>
) => {
  const { name, rules, shouldUnregister, control, defaultValue, ...rest } =
    props;

  const {
    field,
    fieldState: { error, invalid },
  } = useController({
    name,
    rules,
    shouldUnregister,
    defaultValue,
    control,
  });

  const errorMessage = error?.message || false;

  return (
    <TextField
      {...rest}
      helperText={errorMessage && <Translates>{errorMessage}</Translates>}
      error={invalid}
      onChange={field.onChange}
      onBlur={field.onBlur}
      value={field.value || ""}
      name={field.name}
      inputRef={field.ref}
    />
  );
};
