import { useCallback, useState } from "react";
import { useController } from "react-hook-form";

import {
  OutlinedInput,
  FormControl,
  InputLabel,
  LinearProgress,
  Box,
  Grid,
  FormHelperText,
} from "components";
import { DictionaryWords } from "dictionary";
import { useGeneratePassword, useVisiblePassword } from "hooks";
import { Translates } from "widget";

import { getLineProgress } from "./PasswordProgressInput.utils";
import type { PasswordProgressInputProps } from "./PasswordProgressInput.interface";

export const PasswordProgressInput = (props: PasswordProgressInputProps) => {
  const { control } = props;

  const {
    field,
    fieldState: { error, invalid },
  } = useController({
    name: "password",
    defaultValue: "",
    control,
  });

  const { value, onBlur, onChange, name, ref } = field;

  const [isFocus, setIsFocus] = useState(false);

  const onClickGeneratePassword = useCallback(
    (password: string) => {
      onChange(password);
    },
    [onChange]
  );

  const { endAdornment: VisiblePassword, ...visiblePassword } =
    useVisiblePassword();
  const { endAdornment: GeneratePassword } = useGeneratePassword({
    onClickGeneratePassword,
  });

  const handleOnFocus = useCallback(() => {
    setIsFocus(true);
  }, []);

  const handleOnBlur = useCallback(() => {
    setIsFocus(false);
    onBlur();
  }, [onBlur]);

  const errorMessage = error?.message || false;

  const { progress, color } = getLineProgress({ value, invalid });

  return (
    <Grid sx={{ marginBottom: 4 }} container flexDirection="column">
      <FormControl variant="outlined" sx={{ flexGrow: 1 }}>
        <InputLabel error={invalid}>
          <Translates>{DictionaryWords.password}</Translates>
        </InputLabel>
        <OutlinedInput
          error={invalid}
          value={value}
          name={name}
          onChange={onChange}
          inputRef={ref}
          {...visiblePassword}
          endAdornment={
            <>
              {GeneratePassword}
              {VisiblePassword}
            </>
          }
          autoComplete="new-password"
          label={<Translates>{DictionaryWords.password}</Translates>}
          onFocus={handleOnFocus}
          onBlur={handleOnBlur}
        />
        <Box
          sx={{
            position: "absolute",
            width: "100%",
            height: "100%",
            borderRadius: 2,
            border: "1px solid transparent",
            overflow: "hidden",
            pointerEvents: "none",
            display: "flex",
            alignItems: "flex-end",
            borderWidth: isFocus ? 0 : 1,
          }}
        >
          <LinearProgress
            color={color}
            variant="determinate"
            value={progress}
            sx={({ palette }) => ({
              height: isFocus ? 4 : 3,
              width: "100%",
              backgroundColor: invalid
                ? palette.error.light
                : palette.outlinedInput.border,
            })}
          />
        </Box>
      </FormControl>
      <FormHelperText
        sx={({ palette, spacing }) => ({
          color: palette?.[color].main,
          marginLeft: spacing(1.5),
          marginRight: spacing(1.5),
        })}
      >
        {(errorMessage || (value && value?.length > 0)) && (
          <Translates>
            {errorMessage || DictionaryWords.strongPassword}
          </Translates>
        )}
      </FormHelperText>
    </Grid>
  );
};
