import { shuffleString, generateRandomNumber } from "utils";

import {
  UPPERCASE_CHARS,
  LOWERCASE_CHARS,
  SPECIAL_CHARS,
  DIGITS,
} from "./generatePassword.constants";
import type { GeneratePassword } from "./generatePassword.interface";

const getChar = (chars: string) => chars[generateRandomNumber(chars.length)];

export const generatePassword = (options: GeneratePassword): string => {
  const { lenght = 10, unclude = {} } = options || {};
  const {
    uppercase = true,
    lowercase = true,
    special = true,
    digit = true,
  } = unclude;

  let password = "";
  let innerLenght = lenght;
  let allChars = "";

  const createChar = (chars: string) => {
    password += getChar(chars);
    innerLenght--;
  };

  if (uppercase) {
    createChar(UPPERCASE_CHARS);
    allChars += UPPERCASE_CHARS;
  }

  if (lowercase) {
    createChar(LOWERCASE_CHARS);
    allChars += LOWERCASE_CHARS;
  }

  if (special) {
    createChar(SPECIAL_CHARS);
    allChars += SPECIAL_CHARS;
  }

  if (digit) {
    createChar(DIGITS);
    allChars += DIGITS;
  }

  while (innerLenght > 0) {
    createChar(allChars);
  }

  return shuffleString(password);
};
