import { useMemo, useState, useCallback } from "react";

import { Visibility, VisibilityOff } from "icons";
import { IconButton, InputAdornment } from "@mui/material";

import type { HandleMouseDownPassword } from "./useVisiblePassword.interface";

export const useVisiblePassword = () => {
  const [showPassword, setShowPassword] = useState(false);

  const handleClickShowPassword = useCallback(() => {
    setShowPassword((show) => !show);
  }, []);

  const handleMouseDownPassword = useCallback(
    (event: HandleMouseDownPassword) => {
      event.preventDefault();
    },
    []
  );

  const typeInput = useMemo(
    () => (showPassword ? "text" : "password"),
    [showPassword]
  );

  const endAdornment = useMemo(() => {
    return (
      <InputAdornment position="end">
        <IconButton
          aria-label="toggle password visibility"
          onClick={handleClickShowPassword}
          onMouseDown={handleMouseDownPassword}
          edge="end"
        >
          {showPassword ? <VisibilityOff /> : <Visibility />}
        </IconButton>
      </InputAdornment>
    );
  }, [handleClickShowPassword, handleMouseDownPassword, showPassword]);

  return {
    type: typeInput,
    endAdornment,
  };
};
