import { forwardRef } from "react";
import { NavLink as NavLinkRouterDom } from "react-router-dom";

import { Link } from "components";

import { NavLinkProps, RefHTMLAnchorElement } from "./NavLink.interface";
import { defaultActiveStyle } from "./NavLink.constants";

export const NavLink = (props: NavLinkProps) => {
  const { children, activeStyle, to, ...rest } = props;

  return (
    <Link
      underline="none"
      {...rest}
      component={forwardRef((linkProps, ref: RefHTMLAnchorElement) => (
        <NavLinkRouterDom
          {...linkProps}
          to={to}
          ref={ref}
          style={({ isActive }) =>
            isActive ? activeStyle || defaultActiveStyle : {}
          }
        >
          {children}
        </NavLinkRouterDom>
      ))}
    />
  );
};
