import type { Theme } from "@mui/material";

export const sxSelect = ({ spacing, palette, typography }: Theme) => ({
  background: palette.grey[500],

  "&.Mui-focused": {
    backgroundColor: palette.black.main,
    "& .MuiOutlinedInput-notchedOutline": {
      borderColor: "transparent",
    },
  },

  "& .MuiSelect-select": {
    display: "flex",
    alignItems: "center",
    gap: spacing(1.5),
    padding: `${spacing(0.65)} ${spacing(1)} ${spacing(0.65)} ${spacing(1.5)}`,
    boxSizing: "border-box",
    color: palette.common.white,
    fontSize: typography.pxToRem(13),
    textTransform: "uppercase",
  },

  "& .MuiSvgIcon-root": {
    fill: palette.common.white,
  },
});

export const sxMenuItem = ({ palette, spacing, typography }: Theme) => ({
  position: "relative",
  display: "flex",
  alignItems: "center",
  gap: spacing(1),
  padding: `${spacing(1)} ${spacing(1)} ${spacing(1)} ${spacing(1.5)}`,
  boxSizing: "border-box",
  color: palette.common.white,
  fontSize: typography.pxToRem(13),
  textTransform: "uppercase",
  backgroundColor: palette.black.main,

  "&.Mui-selected": {
    backgroundColor: palette.black.main,

    "&:hover": {
      backgroundColor: palette.grey[800],
    },

    "&.Mui-focusVisible": {
      backgroundColor: palette.grey[800],
    },
  },

  "&.Mui-focusVisible": {
    backgroundColor: palette.grey[800],
  },

  "&:hover": {
    backgroundColor: palette.grey[800],
  },

  ":after": {
    content: "' '",
    position: "absolute",
    padding: `0 ${spacing(1.5)}`,
    width: `calc(100% - ${spacing(3)})`,
    bottom: 0,
    border: `1px solid rgba(255, 255, 255, 0.1)`,
  },

  ":first-of-type": {
    paddingTop: spacing(1.5),
  },

  ":nth-last-of-type(1)": {
    ":after": {
      content: "none",
    },

    paddingBottom: spacing(1.5),
  },
});
