import type { Components, Theme } from "@mui/material";

export const MuiOutlinedInput: Components<Theme>["MuiOutlinedInput"] = {
  styleOverrides: {
    root: ({ theme }) => ({
      backgroundColor: theme.palette.outlinedInput.background,
      borderRadius: 8,

      "&.Mui-error": {
        backgroundColor: theme.palette.error.light,
      },
    }),
    notchedOutline: ({ theme }) => ({
      borderColor: theme.palette.outlinedInput.border,
      fontWeight: 500,
    }),
    input: ({ theme }) => ({
      ":-webkit-autofill": {
        WebkitBoxShadow: `inset 0 0 0 50px ${theme.palette.outlinedInput.background}`,
      },
      height: "1.875em",
    }),
  },
};
