import { HashRouter, Routes, Route } from "react-router-dom";
import { Provider } from "react-redux";
import { ThemeProvider } from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";

import { RoutesMap } from "routes";
import { AuthorizationLayout, BaseLayout } from "layout";
import { Login, Register } from "page";
import { LanguageProvider, CheckAuthorization } from "widget";

import { store } from "./store";
import { theme } from "./theme";

import "./theme/base.css";

export function App() {
  return (
    <HashRouter>
      <ThemeProvider theme={theme}>
        <Provider store={store}>
          <CssBaseline />
          <Routes>
            <Route element={<LanguageProvider />}>
              <Route element={<BaseLayout />}>
                <Route element={<AuthorizationLayout />}>
                  <Route path={RoutesMap.login} element={<Login />} />
                  <Route path={RoutesMap.register} element={<Register />} />
                </Route>
                <Route element={<CheckAuthorization />}>
                  <Route path={RoutesMap.home} element={<div>home</div>} />
                </Route>
              </Route>
            </Route>
          </Routes>
        </Provider>
      </ThemeProvider>
    </HashRouter>
  );
}
