import { LOCAL_STORAGE_ACITVE_LANGUAGE } from "constant";
import { DictionaryLanguage } from "dictionary";

export const getLanguage = () => {
  return localStorage.getItem(
    LOCAL_STORAGE_ACITVE_LANGUAGE
  ) as DictionaryLanguage;
};

export const setLanguage = (language: DictionaryLanguage) => {
  return localStorage.setItem(LOCAL_STORAGE_ACITVE_LANGUAGE, language);
};
