import { TextField as TextFieldMui } from "@mui/material";

import type { TextFieldProps } from "./textField.interface";

export const TextField = (props: TextFieldProps) => {
  return (
    <TextFieldMui
      variant="outlined"
      {...props}
      InputProps={{
        autoComplete: "off",
        ...props.InputProps,
      }}
    />
  );
};
