import { generateRandomNumber } from "utils";

import type { Shuffle, ShuffleString } from "./shuffle.interface";

export function shuffle<T>(arg: Shuffle<T>) {
  let inputArray = [...arg];

  for (let i = inputArray.length - 1; i > 0; i--) {
    let randomIndex = generateRandomNumber(i + 1);
    let tempVariable = inputArray[i];
    inputArray[i] = inputArray[randomIndex];
    inputArray[randomIndex] = tempVariable;
  }

  return inputArray;
}

export const shuffleString = (arg: ShuffleString) =>
  shuffle(arg.split("")).join("");
