import { useCallback } from "react";
import { useFormState } from "react-hook-form";

import { Button } from "components";
import { INPUT_MASK_PHONE } from "constant";
import { KeyboardArrowLeft } from "icons";
import { TypographyDictionary, Translates, FormInputMask } from "widget";

import { Steps } from "../../register.constants";

import type { StepTwoProps } from "./stepTwo.interface";

export const StepTwo = (props: StepTwoProps) => {
  const { onChangeStep, control } = props;

  const { errors } = useFormState({ control });

  const handleOnClickSendSms = useCallback(() => {
    onChangeStep(Steps.three);
  }, [onChangeStep]);

  const handleOnChangeStep = useCallback(
    (step: Steps) => () => {
      onChangeStep(step);
    },
    [onChangeStep]
  );

  return (
    <>
      <TypographyDictionary
        variant="body1"
        sx={{ lineHeight: 1.8, letterSpacing: "0.085em", marginBottom: 2 }}
      >
          
      </TypographyDictionary>
      <FormInputMask
        autoFocus
        name="phone"
        control={control}
        fullWidth
        label={<Translates></Translates>}
        maskOptions={{
          mask: INPUT_MASK_PHONE,
        }}
        sx={{ marginBottom: 2.5 }}
        InputProps={{
          sx: {
            minHeight: 63,
          },
        }}
      />
      <Button
        onClick={handleOnClickSendSms}
        fullWidth
        variant="contained"
        size="large"
        color="black"
        disabled={Boolean(errors?.phone)}
        sx={{ marginBottom: 3.5, textTransform: "none" }}
      >
        <TypographyDictionary
          color="white"
          sx={{
            lineHeight: 2.6,
            letterSpacing: "0.07em",
          }}
        >
           
        </TypographyDictionary>
      </Button>
      <Button
        onClick={handleOnChangeStep(Steps.one)}
        variant="text"
        sx={{
          textTransform: "none",
          alignSelf: "flex-start",
          marginLeft: -1,
          marginBottom: -1,
        }}
      >
        <KeyboardArrowLeft />
        <TypographyDictionary
          sx={({ palette }) => ({
            color: palette.black.main,
            opacity: 0.6,
            padding: 0,
          })}
        >
          
        </TypographyDictionary>
      </Button>
    </>
  );
};
