import { useState } from "react";
import { yupResolver } from "@hookform/resolvers/yup";
import { useForm } from "react-hook-form";

import { AuthorizationHeaderLayout } from "layout";
import { Grid } from "components";

import type { OnSubmitRegister, FormRegister } from "./register.interface";
import { StepOne, StepTwo, StepThree, StepFour } from "./children";
import { schema } from "./register.schema";
import { Steps, SubjectOfActivity } from "./register.constants";

export const Register = () => {
  const [step, setStep] = useState<Steps>(Steps.one);

  const { control, handleSubmit, formState, getValues } = useForm<FormRegister>(
    {
      resolver: yupResolver(schema),
      mode: "all",
      defaultValues: {
        subjectOfActivity: SubjectOfActivity.individualEntity,
        phone: undefined,
        smsCode: undefined,
        name: undefined,
        email: undefined,
        password: undefined,
      },
    }
  );

  const onSubmit: OnSubmitRegister = (data) => {
    console.log("onsubmit? ", data);
  };

  const onChangeStep = (newStep: Steps) => {
    setStep(newStep);
  };

  return (
    <>
      <AuthorizationHeaderLayout title={""} />
      <form onSubmit={handleSubmit(onSubmit)}>
        <Grid container flexDirection="column">
          {step === Steps.one && (
            <StepOne onChangeStep={onChangeStep} control={control} />
          )}
          {step === Steps.two && (
            <StepTwo onChangeStep={onChangeStep} control={control} />
          )}
          {step === Steps.three && (
            <StepThree onChangeStep={onChangeStep} control={control} />
          )}
          {step === Steps.four && (
            <StepFour onChangeStep={onChangeStep} control={control} />
          )}
          <button
            onClick={() => {
              console.log(formState.errors, getValues());
            }}
          >
            click
          </button>
        </Grid>
      </form>
    </>
  );
};
