import { memo, useContext } from "react";

import { MenuItem, Select } from "components";
import { DictionaryLanguage } from "dictionary";
import { enumToArray } from "utils";
import { KeyboardArrowDown } from "icons";

import { LanguageContext } from "../";

import { MapIconsLanguage } from "./LanguageSwitcher.constants";
import { sxSelect, sxMenuItem } from "./LanguageSwitcher.sx";
import type { HandleOnChange } from "./LanguageSwitcher.interface";

export const LanguageSwitcher = memo(() => {
  const { activeLanguage, setActiveLanguage } = useContext(LanguageContext);

  const handleOnChange = (event: HandleOnChange) => {
    const value = event.target.value as DictionaryLanguage;
    setActiveLanguage(value);
  };

  return (
    <Select
      value={activeLanguage}
      onChange={handleOnChange}
      IconComponent={({ ...props }) => <KeyboardArrowDown {...props} />}
      sx={sxSelect}
      MenuProps={{
        PaperProps: {
          sx: {
            marginTop: 0.5,
            borderRadius: 2,
          },
        },
        MenuListProps: {
          sx: {
            padding: 0,
          },
        },
      }}
    >
      {enumToArray(DictionaryLanguage).map((lang: DictionaryLanguage) => {
        const Icon = MapIconsLanguage[lang];

        return (
          <MenuItem key={lang} value={lang} sx={sxMenuItem}>
            <Icon />
            {lang}
          </MenuItem>
        );
      })}
    </Select>
  );
});
