import { useCallback } from "react";
import { useController } from "react-hook-form";

import {
  Grid,
  Button,
  ToggleButton,
  ToggleButtonGroup,
  FormHelperText,
} from "components";
import { TypographyDictionary, NavLink, Translates } from "widget";
import { RoutesMap } from "routes";
import { Building, User } from "icons";

import { SubjectOfActivity, Steps } from "../../register.constants";

import { sxToggleButton } from "./stepOne.sx";
import type { StepOneProps, _MouseEvent } from "./stepOne.interface";

export const StepOne = (props: StepOneProps) => {
  const { onChangeStep, control } = props;
  const {
    field,
    fieldState: { error, invalid },
  } = useController({ name: "subjectOfActivity", control });

  const handleChange = useCallback(
    (_event: _MouseEvent, newSubject: SubjectOfActivity) => {
      field.onChange(newSubject);
    },
    [field]
  );

  const handleOnChangeStep = useCallback(
    (step: Steps) => () => {
      onChangeStep(step);
    },
    [onChangeStep]
  );

  const errorMessage = error?.message || false;

  return (
    <>
      <TypographyDictionary
        variant="body1"
        sx={{
          lineHeight: 1.8,
          letterSpacing: "0.085em",
          marginBottom: 2,
        }}
      >
          
      </TypographyDictionary>
      <Grid
        sx={{
          marginBottom: 2.5,
        }}
      >
        <ToggleButtonGroup
          value={field.value}
          exclusive
          onChange={handleChange}
          aria-label="Platform"
          fullWidth
          sx={{
            columnGap: 1.5,
            display: "flex",
            minHeight: 63,
          }}
        >
          <ToggleButton
            value={SubjectOfActivity.individualEntity}
            sx={sxToggleButton}
          >
            <User />
            <TypographyDictionary color={({ palette }) => palette.black.main}>
              
            </TypographyDictionary>
          </ToggleButton>
          <ToggleButton
            value={SubjectOfActivity.legalEntity}
            sx={sxToggleButton}
          >
            <Building />
            <TypographyDictionary color={({ palette }) => palette.black.main}>
              
            </TypographyDictionary>
          </ToggleButton>
        </ToggleButtonGroup>
        {errorMessage && (
          <FormHelperText error={invalid}>
            <Translates>{errorMessage}</Translates>
          </FormHelperText>
        )}
      </Grid>

      <Button
        onClick={handleOnChangeStep(Steps.two)}
        fullWidth
        variant="contained"
        size="large"
        color="black"
        disabled={invalid}
        sx={{ marginBottom: 2.375, textTransform: "none" }}
      >
        <TypographyDictionary color="white"></TypographyDictionary>
      </Button>
      <NavLink
        to={RoutesMap.login}
        variant="outlined"
        sx={{
          letterSpacing: "0.085em",
        }}
      >
        <Translates> ? </Translates>
      </NavLink>
    </>
  );
};
