import type { Components, Theme } from "@mui/material";

export const MuiFormLabel: Components<Theme>["MuiFormLabel"] = {
  styleOverrides: {
    root: ({ ownerState }) => {
      const dataShrink = ownerState["data-shrink"];

      if (!dataShrink) {
        return {
          lineHeight: "2.1em",
          letterSpacing: "0.03em",
          left: 2,
        };
      }
    },
  },
};
