import { object, string } from "yup";

import { REG_EXP_INPUT_MASK_PHONE, FORM_MIN_LENGTH_PASSWORD } from "constant";
import { DictionaryWords } from "dictionary";

import { SubjectOfActivity } from "./register.constants";

export const schema = object({
  subjectOfActivity: string()
    .oneOf([SubjectOfActivity.individualEntity, SubjectOfActivity.legalEntity])
    .required(DictionaryWords.formRequired),
  phone: string().when("subjectOfActivity", {
    is: (val: SubjectOfActivity) => Boolean(val),
    then: (schema) =>
      schema
        .required(DictionaryWords.formRequired)
        .matches(REG_EXP_INPUT_MASK_PHONE, DictionaryWords.formPhoneMatches),
  }),
  smsCode: string().when("phone", {
    is: (val: string) => Boolean(val),
    then: (schema) => schema.required(DictionaryWords.formRequired),
  }),
  name: string().when("smsCode", {
    is: (val: string) => Boolean(val),
    then: (schema) => schema.required(DictionaryWords.formRequired),
  }),
  email: string().when("smsCode", {
    is: (val: string) => Boolean(val),
    then: (schema) =>
      schema
        .required(DictionaryWords.formRequired)
        .email(DictionaryWords.formEmail),
  }),
  password: string().when("smsCode", {
    is: (val: string) => Boolean(val),
    then: (schema) =>
      schema
        .required(DictionaryWords.formRequired)
        .min(FORM_MIN_LENGTH_PASSWORD, DictionaryWords.formPasswordMin),
  }),
});
