import { memo, useContext } from "react";

import { MapDictonary } from "dictionary";
import { LanguageContext } from "widget";

import type { TranslatesProps } from "./translates.interface";

export const Translates = memo((props: TranslatesProps) => {
  const { children } = props;

  const { activeLanguage } = useContext(LanguageContext);

  const activeDictonary = MapDictonary.get(activeLanguage);
  const text = activeDictonary
    ? activeDictonary[children] ?? children
    : children;

  return <>{text}</>;
});
